.TodoSearch {
    background: #F9FBFC;
    border-radius: 10px;
    border: none;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline: none;
  }

  @media (min-width:767px){
    .TodoSearch{
      display: flex;
      justify-items: center;
      margin: 0 auto;
      width: 700px;
    }
  }